import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import styled from 'styled-components';

const ContentContainer = styled.div`
    display: flex;
    flex-direction: column;
    margin 20px;
`

const ExperienceTitle = styled.p`
    font-size: 22px;
    color: #010420 !important;
    font-weight: bold;
`

const ExperienceDate = styled.p`
    margin: 10px 0px;
    font-size: 16px;
    text-transform: uppercase;
    color: #E09DF5;
`

const ExperienceInfo = styled.li`
    font-size: 16px;
    color: #010420;
    list-style-type: circle;
`

const ExperienceTech = styled.p`
    margin: 10px 0px;
    font-size: 14px;
    text-transform: uppercase;
    color: #E09DF5;
`

function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function SimpleTabs() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Tabs value={value} onChange={handleChange} aria-label="work experience">
          <Tab label="Healthfirst" {...a11yProps(0)} />
          <Tab label="Mission Throttle" {...a11yProps(1)} />
          <Tab label="AllianceBernstein" {...a11yProps(2)} />
          <Tab label="AIESEC" {...a11yProps(3)} />
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0}>
        <ContentContainer>
            <ExperienceTitle>Software Engineer Intern @ Healthfirst</ExperienceTitle>
            <ExperienceDate>June 2020 - August 2020</ExperienceDate>
            <ExperienceInfo>Worked in an Agile team for the Customer Digital Experience program. We utilized a human-centered design approach to innovate digital solutions to assist business operations during the COVID pandemic and beyond.</ExperienceInfo>
            <ExperienceInfo>Contributed to the ideation, design, and development of an internally-used iOS application that will be put into production and improve the experience of millions of customers.</ExperienceInfo>
            <ExperienceInfo>Delivered new functionality in two week sprints and presented to the executive leadership team.</ExperienceInfo>
            <ExperienceTech>Swift React.js Node.js Twilio SendGrid Postman Figma GitHub</ExperienceTech>
        </ContentContainer>
      </TabPanel>
      <TabPanel value={value} index={1}>
        <ContentContainer>
            <ExperienceTitle>Student Software Engineer @ Mission Throttle</ExperienceTitle>
            <ExperienceDate>January 2019 - January 2020</ExperienceDate>
            <ExperienceInfo>Worked in an Agile design team that developed and published a plugin that allows for SMS distribution through an intuitive interface when using the open-source software, Limesurvey.</ExperienceInfo>
            <ExperienceInfo>Utilized a human-centered design (HCD) framework in designing and developing the plugin.</ExperienceInfo>
            <ExperienceTech>PHP Javascript Twilio GitHub Figma Trello</ExperienceTech>
        </ContentContainer>
      </TabPanel>
      <TabPanel value={value} index={2}>
        <ContentContainer>
            <ExperienceTitle>Frontend Developer Intern @ AllianceBernstein</ExperienceTitle>
            <ExperienceDate>June 2019 - August 2020</ExperienceDate>
            <ExperienceInfo>Worked in digital development team to build firm-wide Design System that  empowers employees to create and build web-apps that follow brand guidelines in an easy way.</ExperienceInfo>
            <ExperienceInfo>Contributed by writing reusable code snippets for various components, and building pages of the design system itself.</ExperienceInfo>
            <ExperienceTech>React.js Node.js Git Azure Jira</ExperienceTech>
        </ContentContainer>
      </TabPanel>
      <TabPanel value={value} index={3}>
        <ContentContainer>
            <ExperienceTitle>VP Talent Management @ AIESEC</ExperienceTitle>
            <ExperienceDate>January 2019 - December 2019</ExperienceDate>
            <ExperienceInfo>Developed and published an elaborate tracking tools that measures the KPIs and goal achievement of the 40+ members within organization; these tools were later used on a national scale.</ExperienceInfo>
            <ExperienceInfo>Analyzed data to innovate strategies and drive new business practices that helped increased the efficiency of the organization by over 100%.</ExperienceInfo>
            <ExperienceInfo>Head organizer of highly competitive recruitment process of over 50 applicants.</ExperienceInfo>
            <ExperienceTech>Excel GSuite Survey Monkey</ExperienceTech>
        </ContentContainer>
      </TabPanel>
    </div>
  );
}